#drawer-button {
  position: fixed;
  top: 32px;
  left: 32px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
#drawer-button svg {
  fill: var(--accent-color);
  width: 32px;
  height: 32px;
  cursor: pointer;
}

@media (max-width: 600px) and (orientation: portrait) {
  #drawer-button {
    top: 16px;
    left: 16px;
  }
  #drawer-button svg {
    fill: var(--display-text);
  }
}



